<template>
    <div class="container table-container">
        <div class="row "> 
        <table class="table table-striped table-hover">
            <thead>
                <tr>
                    <th>序号</th>
                    <th v-for="header in headerMappingTableHeaders" :key="header.id" >{{ header }}</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(responseData,responseIndex) in responseDatas" :key="responseData.id">
                    <td>{{ responseIndex +1 }}</td>
                    <td v-for="tableHeader in tableHeaders" :key="tableHeader.id" >{{ responseData[tableHeader] }}</td>
                </tr>
            </tbody>
            </table>
      </div>

    <!-- 底部分页 -->
    <nav aria-label="Page navigation example">
      <ul class="pagination">
        <li class="page-item">
          <a v-if="currentPage == 1" class="page-link disabled" href="#" aria-label="Previous" @click.prevent="goPrevious">
            <span aria-hidden="true">&laquo;</span>
          </a>
          <a v-else class="page-link" href="#" aria-label="Previous" @click.prevent="goPrevious">
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li>
        <li class="page-item" v-for="(page) in totalPagesArray" :key="page">
            <a class="page-link" href="#" @click.prevent="goToPage(page)">{{ page }}</a>
        </li>
        <li class="page-item">
          <a v-if="currentPage == totalPages" class="page-link disabled" href="#" aria-label="Next" @click.prevent="goNext">
            <span aria-hidden="true">&raquo;</span>
          </a>
          <a v-else class="page-link" href="#" aria-label="Next" @click.prevent="goNext">
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </nav>
</div>
  </template>
  
  <script>
  import $ from'jquery'
  import {ref} from 'vue'
  export default {
    name: 'LogView',
    setup() {
        const responseDatas = ref([]); // 使用ref来存储响应数据
        const allDatas = ref([]); // 存储从后端一次性获取的所有数据
        const tableHeaders = ref([]); // 使用ref来存储表格头部
        const headerMappingTableHeaders = ref([]); // headerMapping后的头部
        const currentPage = ref(1)  //页码
        const pageSize = 10; // 每页显示的记录数
        const totalPages = ref(0)
        const totalPagesArray = ref([])
        const goPrevious = () => {
            if (currentPage.value > 1) {
                currentPage.value -= 1;
                updateDisplayedData();
            }
        };
        const goToPage = (page) => {
            if (page >= 1 && page <= Math.ceil(allDatas.value.length / pageSize)) {
                currentPage.value = page;
                updateDisplayedData();
            }
        };
        const goNext = () => {
            if (currentPage.value < Math.ceil(allDatas.value.length / pageSize)) {
                currentPage.value += 1;
                updateDisplayedData();
            }
        };

        const headerMapping = {
            'user':'用户名',
            'ip_address':'IP地址',
            'user_agent':'User-Agent',
            'login_time':'登录时间'
        };
        const updateDisplayedData = () => {
        const startIndex = (currentPage.value - 1) * pageSize;
        const endIndex = startIndex + pageSize;
        responseDatas.value = allDatas.value.slice(startIndex, endIndex);
    
        // 更新表格头部（如果数据格式允许）
        if (responseDatas.value.length > 0) {
            tableHeaders.value = Object.keys(responseDatas.value[0]);
            headerMappingTableHeaders.value = Object.keys(responseDatas.value[0]).map(key => {
            // 这里假设有一个全局的 headerMapping 对象来进行映射
            // 如果没有映射，则使用原始键名
            return headerMapping[key] || key;
            });
        }
        };
        const fetchData = () =>{
          $.ajax({
            url: "http://127.0.0.1:8000/log/loginlog/",
            type: "GET",
            success(resp) {
                allDatas.value = resp;
                totalPages.value = Math.ceil(resp.length / 10)
                for(let i = 1;i <= totalPages.value;++i)
                  totalPagesArray.value.push(i)
                console.log(totalPages.value,totalPagesArray.value)
                updateDisplayedData()
            }
            });
          };
        fetchData()

        return {
            responseDatas,
            tableHeaders,
            headerMappingTableHeaders,
            currentPage,
            totalPagesArray,
            goPrevious,
            goToPage,
            goNext,
        };
    },
  };
  </script>
  
  <style scoped>
  .list-inline-item:not(:last-child) {
    margin-right: 10px;
  }
   
  .table-container { /* 设置固定高度和滚动条 */
    width: 100%;
    height: 580px; /* 根据需要调整高度 */
    overflow-y: auto; /* 启用垂直滚动 */
    /* margin-bottom: 20px; 可选，为分页控件留出空间 */
  }
  .pagination{
    float: right;
    margin-right: 10px;
  }
  .error-message {
    margin: auto;
    color: red;
  }
  .disabled {
    pointer-events: none; /* 禁止点击 */
    opacity: 0.65;        /* 可选：降低透明度以表示禁用 */
    cursor: not-allowed;  /* 可选：改变鼠标悬停时的光标样式 */
  }
  th{
    font-size:16px;
    text-align: center; /* 使文字水平居中 */
  }
  td{
    font-size:15px;
    text-align: center; /* 使文字水平居中 */
  }
  </style>