<template>
  <div class="container">
    <ul class="list-group">
    <li
      class="list-group-item"
      v-for="item in menuItems"
      :key="item.name"
      :class="{ active: selectedItem === item.name }"
      @click="selectItem(item)"
    >
      <i :class="item.img"></i>
      {{ item.display }}
    </li>
  </ul>
  </div>
  
</template>
  
  <script>
    import router from '@/router/index';
    import {ref} from 'vue'
  export default {
    name: 'AlisdView',
    setup() {
        const selectedItem = ref('家教管理');
        const menuItems = [
            { name: '家教管理', display: '家教管理', route: "/admin/thing" ,img:"bi bi-card-list"},
            { name: '科目分类', display: '科目分类', route: "/admin/classification",img:"bi bi-columns"},
            { name: '标签管理', display: '标签管理', route: "/admin/tag",img:"bi bi-tag"},
            { name: '评论管理', display: '评论管理', route: "/admin/comment",img:"bi bi-chat-dots"},
            { name: '用户管理', display: '用户管理', route: "/admin/user",img:"bi bi-person"},
            { name: '运营管理', display: '运营管理', route: "/admin/thing",img:"bi bi-folder"},
            // { name: '日志管理', display: '日志管理'},
            { name: '登录管理', display: '登录管理', route: "/admin/loginlog",img:"bi bi-border-all"},
            // { name: '操作管理', display: '操作管理', route: "/admin/oplog",img:"bi bi-border-all" },
            // { name: '错误管理', display: '错误管理', route: "/admin/errorlog" },
            { name: '统计分析', display: '统计分析', route: "/admin/overview",img:"bi bi-house-door"},
            { name: '系统信息', display: '系统信息', route: "/admin/thing",img:"bi bi-exclamation-circle"},
        ];
    
        const selectItem = (item) => {
            selectedItem.value = item.name; // 更新选中的项
            // console.log(item)
            router.push(item.route); // 导航到指定的路由
        };
    
        return {
            selectedItem,
            menuItems,
            selectItem,
        };
    }
  };
  </script>
  
  <style scoped>
  .container{
    height: 100%;
  }
  /* ul{
    background-color: "#001529";
  } */
  .list-group {
    border-radius: 8px;
    flex: 1; /* 占据剩余空间 */
    overflow: hidden;
    margin: 0 auto;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .list-group-item {
    border: none;
    padding: 15px;
    text-align: center;
    transition: background-color 0.3s ease, color 0.3s ease;
    cursor: pointer; /* 鼠标悬停时显示指示 */
  }
  
  .list-group-item.active {
    background-color: #007bff;
    color: white;
  }
  
  .list-group-item:not(.active):hover {
    background-color: #f8f9fa;
    color: #343a40;
  }

  </style>