import { createRouter, createWebHashHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import UserView from '../views/UserView.vue' 
import ThingView from '../views/ThingView.vue'
import CommentView from '../views/CommentView.vue'
import ClassificationView from '../views/ClassificationView.vue'
import TagView from '../views/TagView.vue'
import LoginView from '../views/LoginView.vue'
import LogView from '../views/LoginLogView.vue'
import Overview from '../views/Overview.vue'
import store from '../store';
const isUserLoggedIn = () => {
  if(store.state.user.is_login)
    return true
  else 
    return false
}

const routes = [
  {
    path: '/',
    name: 'home', // 通常将根路径命名为home
    component: HomeView, // 或者您可以选择另一个组件作为默认页面
  },
  {
    path: '/login',
    name: 'login',
    component: LoginView,
  },
  {
    path: '/admin',
    component: HomeView, // 这里假设HomeView作为admin页面的布局或容器
    children: [
      {
        path: 'comment', // 去掉/前缀
        name: 'comment',
        component: CommentView,
        meta: { requiresAuth: true },
      },
      {
        path: 'thing', // 去掉/前缀
        name: 'thing',
        component: ThingView,
        meta: { requiresAuth: true },
      },
      {
        path: 'classification', // 去掉/前缀
        name: 'classification',
        component: ClassificationView,
        meta: { requiresAuth: true },

      },
  //     {
  //       path: '', // 空字符串表示默认子路由
  //       name: 'admin-default', // 可选，为默认子路由命名
  //       redirect: 'thing', // 例如，重定向到thing页面
  //       meta: { requiresAuth: true },
  //     },      
      {
        path: 'user/', 
        name: 'user',
        component: UserView,
        meta: { requiresAuth: true },
      },
      {
        path: 'loginlog/', 
        name: 'loginlog',
        component: LogView,
        meta: { requiresAuth: true },
      },      
      {
        path: 'overview/', 
        name: 'overview',
        component: Overview,
        meta: { requiresAuth: true },
      },
      {
        path: 'tag/', 
        name: 'tag',
        component: TagView,
        meta: { requiresAuth: true },
      },
    ]
  },

]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})
router.beforeEach((to, from, next) => {
  // 检查目标路由是否需要认证
  if (to.matched.some(record => record.meta.requiresAuth)) {
    // 如果需要认证，检查用户是否已登录
    if (!isUserLoggedIn()) {
      // 如果用户未登录，重定向到登录页面
      next({
        path: '/login',
        query: { redirect: to.fullPath } // 可以将当前路由的路径作为查询参数传递给登录页面，以便登录后重定向回来
      })
    } else {
      // 如果用户已登录，允许路由继续
      next()
    }
  } else {
    // 如果目标路由不需要认证，允许路由继续
    next()
  }
})
export default router