<template>
    <div class="container table-container">
      <div class="row">
        <div class="d-flex justify-content-end"> <!-- 使用 d-flex 和 justify-content-end 让按钮靠右 -->
          <button type="button" class="btn btn-danger" @click="deleteComment">批量删除</button>
        </div>
      </div>
  
        <div class="row "> 
        <table class="table table-striped table-hover">
            <thead>
                <tr>
                  <th ><input type="checkbox" id="select-all"  v-model="selectAll" @change="toggleSelectAll"></th> <!-- 添加全选复选框（可选） -->
                  <th v-for="header in headerMappingTableHeaders" :key="header.id" >{{ header }}</th>
              </tr>
            </thead>
            <tbody>
                <tr v-for="(responseData,index) in responseDatas" :key="responseData.id">
                <td><input type="checkbox" class="row-select" v-model="selectedIds"
                  :value="responseData.id"></td> <!-- 添加行选择复选框 -->
                <td>{{ index + 1 + (currentPage - 1) * 10}}</td>
                <td v-for="key in filteredHeaders" :key="key" >{{responseData[key]}}</td>
                <td >
                    <button type="button" class="btn" @click="deleteComment(responseData.id)">删除</button>
                </td>
            </tr>
            </tbody>
            </table>
      </div>
    </div>
    <!-- 底部分页 -->
    <nav aria-label="Page navigation example">
      <ul class="pagination">
        <li class="page-item">
          <a v-if="currentPage == 1" class="page-link disabled" href="#" aria-label="Previous" @click.prevent="goPrevious">
            <span aria-hidden="true">&laquo;</span>
          </a>
          <a v-else class="page-link" href="#" aria-label="Previous" @click.prevent="goPrevious">
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li>
        <li class="page-item" v-for="page in totalPages" :key="page">
            <a class="page-link" href="#" @click.prevent="goToPage(page)">{{ page }}</a>
        </li>
        <li class="page-item">
          <a v-if="currentPage == totalPages" class="page-link disabled" href="#" aria-label="Next" @click.prevent="goNext">
            <span aria-hidden="true">&raquo;</span>
          </a>
          <a v-else class="page-link" href="#" aria-label="Next" @click.prevent="goNext">
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </nav>
  </template>
  
  <script>
  import $ from'jquery'
  import {ref,computed } from 'vue'
  import { useStore } from 'vuex';
  export default {
    name: 'UserView',
    setup() {
        let store = useStore()
        const responseDatas = ref([])
        const totalPages = ref(0) 
        const selectAll = ref(false); // 添加全选状态
        const currentPage = ref(1)  //页码
        const selectedIds = ref([]); // 添加已选行ID数组

        // const error_message = ref('')
  
        const headerMappingTableHeaders = {
          'id':'序号',
          'username':'用户',
          'teachername':'家教姓名',
          'comment':'评论内容',
          'create_time':'评论时间',
        }
  
        const updateCommentList = () => {
          $.ajax({
            url: "http://47.109.76.15:8000/comment/commentlist",
            type: "GET",
            data:{
                index:currentPage.value
            },
            success(resp){
                console.log(resp)
                responseDatas.value = resp
                totalPages.value = Math.ceil(resp[0].count / 10)
            }      
        })
        }
        
        updateCommentList()
  
        const goPrevious = () => {
          if (currentPage.value > 1) {
            currentPage.value = currentPage.value - 1
            updateCommentList();
          }
        }
        const goToPage = (i) =>{
          currentPage.value = i
          updateCommentList()
        }
        const goNext = () => {
          if (currentPage.value < totalPages.value) {
            currentPage.value = currentPage.value + 1
            updateCommentList();
          }
        }

        const filteredHeaders = computed(() => {
          return Object.keys(headerMappingTableHeaders)
            .filter(key => key !== 'id');
        });

        const deleteComment = (comment_id) =>{
          $.ajax({
            url: "http://47.109.76.15:8000/comment/del",
            type: "delete",
            data:{
                comment_id:comment_id
            },
            headers: {
                'Authorization': "Bearer " + store.state.user.access,
              },
            success(resp){
                console.log(resp)
                responseDatas.value = resp
                updateCommentList()
            }      
        })
        }
        return {
          totalPages,
          headerMappingTableHeaders,
          filteredHeaders,
          responseDatas,
          selectAll,
          selectedIds,
          goPrevious,
          goToPage,
          goNext,
          currentPage,
          deleteComment
        };
    },
  };
  </script>
  
  <style scoped>
  
  .table-container { /* 设置固定高度和滚动条 */
    width: 100%;
    height: 580px; 
    overflow-y: auto; 
  }
  
  .btn{
    margin-right: 20px;
  }
  .pagination{
    float: right;
    margin-right: 10px;
  }
  .error-message {
    margin: auto;
    color: red;
  }
  .disabled {
    pointer-events: none; /* 禁止点击 */
    opacity: 0.65;        /* 可选：降低透明度以表示禁用 */
    cursor: not-allowed;  /* 可选：改变鼠标悬停时的光标样式 */
  }
  th{
    font-size:16px;
    text-align: center; /* 使文字水平居中 */
  }
  td{
    font-size:14px;
    text-align: center; /* 使文字水平居中 */
  }
  </style>