import $ from 'jquery';
import {jwtDecode} from 'jwt-decode';

const ModuleUser = {
  state: {
    id: "",
    username: "",
    avatar: "",
    access: "",
    refresh: "",
    nickname:'',
    role:1,
    is_login: false,
  },
  getters: {
  },
  mutations: {
      updateUser(state, user) {
          state.id = user.id;
          state.username = user.username;
          state.nickname = user.nickname;
          state.avatar = user.avatar;
          state.access = user.access;
          state.refresh = user.refresh;
          state.role = user.role;
          state.is_login = user.is_login;
      },
      updateAccess(state, access) {
          state.access = access;
      },
      logout(state) {
          state.id = "";
          state.username = "";
          state.nickname = "";
          state.role = 1;
          state.avatar = "";
          state.access = "";
          state.refresh = "";
          state.is_login = false;
      }
  },
  actions: {
      login(context, data) {
        $.ajax({
            url: "http://47.109.76.15:8000/user/token/",
            type: "POST",
            data: {
                username: data.username,
                password: data.password,
            },
            success(resp) {
                const {access, refresh} = resp;
                const access_obj = jwtDecode(access);

                setInterval(() => {
                    $.ajax({
                        url: "http://47.109.76.15:8000/user/token/refresh/",
                        type: "POST",
                        data: {
                            refresh,
                        },
                        success(resp) {
                            context.commit('updateAccess', resp.access);
                        }
                    });
                }, 4.5 * 60 * 1000);
                $.ajax({
                    url: "http://47.109.76.15:8000/user/userinfo/",
                    type: "GET",
                    data: {
                        user_id: access_obj.user_id,
                    },
                    headers: {
                        'Authorization': "Bearer " + access,
                    },
                    success(resp) {
                        console.log(resp)
                        if(resp.role == '管理员'){
                            context.commit("updateUser", {
                                ...resp,
                                access: access,
                                refresh: refresh,
                                is_login: true,
                            });
                            data.success();
                        }
                        else{
                            data.error();
                        }
                    },
                })
            },
            error() {
                data.error();
            }
        });
      },
  },
  modules: {
  }
};

export default ModuleUser;