<template>
    <div class="container table-container">
      <div class="row">
        <div class="d-flex justify-content-end"> <!-- 使用 d-flex 和 justify-content-end 让按钮靠右 -->
          <button type="button" class="btn btn-primary" data-bs-toggle="modal" data-bs-target="#staticBackdrop">新增</button>
          <button type="button" class="btn btn-danger" @click="getids">批量删除</button>
        </div>
      </div>
        <div class="row"> 
        <table class="table table-striped table-hover">
            <thead>
                <tr>
                  <td ><input type="checkbox" id="select-all"  v-model="selectAll" @change="toggleSelectAll"></td> <!-- 添加全选复选框（可选） -->
                  <td v-for="(header, key) in headerMappingTableHeaders" :key="key">{{ header }}</td>
                </tr>
            </thead>
            <tbody>
              <tr v-for="(responseData,index) in responseDatas" :key="responseData.id">
                <td><input type="checkbox" class="row-select" v-model="selectedIds" :value="index + 1 + (currentPage - 1) * 10"></td> <!-- 用于选择行的复选框 -->
                <td v-for="(header, key) in filteredHeaders" :key="key">{{ responseData[header] }}</td>
                <td >                    
                    <button type="button" class="btn" @click="deleteClass(index)">删除</button>
                </td>
              </tr>
            </tbody>
                
          </table>
      </div>
    </div>
    <!-- 底部分页 -->
    <nav aria-label="Page navigation example">
      <ul class="pagination">
        <li class="page-item">
          <a v-if="currentPage == 1" class="page-link disabled" href="#" aria-label="Previous" @click.prevent="goPrevious">
            <span aria-hidden="true">&laquo;</span>
          </a>
          <a v-else class="page-link" href="#" aria-label="Previous" @click.prevent="goPrevious">
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li>
        <li class="page-item" v-for="page in totalPages" :key="page">
            <a class="page-link" href="#" @click.prevent="goToPage(page)">{{ page }}</a>
        </li>
        <li class="page-item">
          <a v-if="currentPage == totalPages" class="page-link disabled" href="#" aria-label="Next" @click.prevent="goNext">
            <span aria-hidden="true">&raquo;</span>
          </a>
          <a v-else class="page-link" href="#" aria-label="Next" @click.prevent="goNext">
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </nav>
    <!-- Modal -->
    <div class="modal fade" id="staticBackdrop" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog">
        <div class="modal-content">
        <div class="modal-header">
            <h1 class="modal-title fs-5" id="exampleModalLabel">新增</h1>
            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
            <div class="row mb-3">
                <div class="col-3">
                  <label class="form-label">分类名称:</label>
                </div>
                <div class="col-9" >
                  <input type="text" class="form-control" placeholder="请输入" v-model="classname">
                </div>
              </div>
        </div>
        <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" @click="resetclassgname">取消</button>
            <button type="button" class="btn btn-primary"  @click="addClass">确定</button>
        </div>
        </div>
    </div>
    </div>

    
    
  </template>
  
  <script>
  import $ from'jquery'
  import {ref,computed } from 'vue'
  import { useStore } from 'vuex';
  export default {
    name: 'UserView',
    setup() {
        let store = useStore()
        const responseDatas = ref([])
        const totalPages = ref(0) 
        const selectAll = ref(false); // 添加全选状态
        const currentPage = ref(1)  //页码
        const selectedIds = ref([]); // 添加已选行ID数组
        const error_message = ref('')
        const classname = ref('')
        const headerMappingTableHeaders = {
          'classname':'分类名称',
          'operate':'操作',
        }

        const updateClassList = () => {
          $.ajax({
            url: "http://47.109.76.15:8000/classification/classificationlist/",
            type: "GET",
            success(resp){
                responseDatas.value = resp
                console.log(resp,resp.length)
                totalPages.value = Math.ceil(resp.length / 10)
            }      
        })
        }
        
        const filteredHeaders = computed(() => {
          return Object.keys(headerMappingTableHeaders)
            .filter(key => key !== 'id' && key !== 'operate');
        });
        
        const goPrevious = () => {
          if (currentPage.value > 1) {
            currentPage.value = currentPage.value - 1
            updateClassList();
          }
        }
        const goToPage = (i) =>{
          currentPage.value = i
          updateClassList()
        }
        const goNext = () => {
          if (currentPage.value < totalPages.value) {
            currentPage.value = currentPage.value + 1
            updateClassList();
          }
        }




        const addClass = () =>{
            console.log('classname.value',classname.value)

            $.ajax({
            url: "http://47.109.76.15:8000/classification/addclassification/",
            type: "post",
            data:{
              'classname': classname.value
            },
            headers:{
              'Authorization': "Bearer " + store.state.user.access,
            },
            success(resp){
              console.log(resp)
              $('#staticBackdrop').hide()
              updateClassList()
              resetclassgname()
            }
          })
        }

        const resetclassgname = ()=>{
            classname.value = ''
        }
        const deleteClass = (index) =>{
            const names = responseDatas.value[index]['classname']
            console.log(index,'-----------',names)
            $.ajax({
                url: "http://47.109.76.15:8000/classification/delclassification/",
                type: "delete",
                data:{
                    'classname': names
                },
                headers:{
                    'Authorization': "Bearer " + store.state.user.access,
                },
                success(resp){
                    console.log('delete',resp)
                    updateClassList()
                }
            })
        }
        
        updateClassList()
        return {
          totalPages,
          headerMappingTableHeaders,
          responseDatas,
          filteredHeaders,
          selectAll,
          selectedIds,
          goPrevious,
          goToPage,
          goNext,
          currentPage,
          error_message,
          deleteClass,
          classname,
          resetclassgname,
          addClass
        };
    },
  };
  </script>
  
  <style scoped>

    .table-container { /* 设置固定高度和滚动条 */
        width: 100%;
        height: 580px; 
        overflow-y: auto; 
    }

    .btn{
        margin-right: 20px;
    }
    .pagination{
        float: right;
        margin-right: 10px;
    }
    .error-message {
        margin: auto;
        color: red;
    }
    .disabled {
        pointer-events: none; /* 禁止点击 */
        opacity: 0.65;        /* 可选：降低透明度以表示禁用 */
        cursor: not-allowed;  /* 可选：改变鼠标悬停时的光标样式 */
    }

    td {
        text-align: left; /* 设置文字靠左显示 */
        padding: 8px; /* 添加内边距以增加可读性 */
        font-size:14px;
    }
    td:nth-child(2) {
        width: 80%; /* 设置第二个单元格的宽度为60%（或你需要的任何百分比） */
    }
    /* 可选：设置其他单元格的宽度以确保它们不会挤压第二个单元格 */
    td:nth-child(1), td:nth-child(3) {
        width: 10%; /* 例如，设置第一个和第三个单元格的宽度为20% */
    }
  </style>