<template>
    <nav class="navbar navbar-expand-lg bg-body-tertiary">
      <div class="container">
        <a class="navbar-brand" href="#">家教后台管理系统</a>
        <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarText" aria-controls="navbarText" aria-expanded="false" aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
        </button>
  
        <div class="collapse navbar-collapse" id="navbarText">
          <ul class="navbar-nav ms-auto"> <!-- 使用 ms-auto 将列表推向右侧 -->
            <li class="nav-item">
              <span class="nav-link navbar-text">管理员[{{ username }}]</span>
            </li>
            <li class="nav-item">
              <button class="btn btn-link" @click="logout">退出</button>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  </template>
  
  <script>
  import { useStore } from 'vuex';
  import { ref } from 'vue';
  import router from '@/router/index';

  export default {
    name: 'HeaderView',
    setup() {
      const store = useStore();
      const username = ref(store.state.user.nickname);
 
      const logout = () => {
        store.commit('logout');
        router.push({name: 'login'});
      };
  
      return {
        username,
        logout,
      };
    },
  };
  </script>
  
  <style scoped>
  /* 你的样式 */
  </style>