<template>
  <div class="container table-container">
    <div class="row">
      <div class="d-flex justify-content-end"> <!-- 使用 d-flex 和 justify-content-end 让按钮靠右 -->
        <button type="button" class="btn btn-primary" data-bs-toggle="modal" data-bs-target="#staticBackdrop" @click="toAddUser">新增</button>
        <!-- @click="deleteUser" -->
        <button type="button" class="btn btn-danger" @click="getids">批量删除</button>
        <form class="d-flex" role="search">
          <input class="form-control me-2" type="search" v-model="searchData"  placeholder="请输入教师名" aria-label="Search">
          <button class="btn btn-outline-success" @click="search">
            <i class="bi bi-search"></i>
          </button>
       </form>
      </div>
    </div>
      <div class="row"> 
      <table class="table table-striped table-hover">
          <thead>
              <tr>
                <th ><input type="checkbox" id="select-all"  v-model="selectAll" @change="toggleSelectAll"></th> <!-- 添加全选复选框（可选） -->
                <th v-for="(header, key) in headerMappingTableHeaders" :key="key">{{ header }}</th>
              </tr>
          </thead>
          <tbody>
            <tr v-for="(responseData,index) in responseDatas" :key="responseData.id">
              <td><input type="checkbox" class="row-select" v-model="selectedIds" :value="index + 1 + (currentPage - 1) * 10"></td> <!-- 用于选择行的复选框 -->
              <td>{{ index + 1 + (currentPage - 1) * 10}}</td>
              <td v-for="(header, key) in filteredHeaders" :key="key">{{ responseData[header] }}</td>
              <td >
                  <button type="button" class="btn" data-bs-toggle="modal" data-bs-target="#staticBackdrop" @click="updateUser(responseData.id)">编辑</button>
                  
                  <button type="button" class="btn" @click="deleteUser(responseData.id)">删除</button>
              </td>
            </tr>
          </tbody>
              
        </table>
    </div>
  </div>
  <!-- 底部分页 -->
  <nav aria-label="Page navigation example">
    <ul class="pagination">
      <li class="page-item">
        <a v-if="currentPage == 1" class="page-link disabled" href="#" aria-label="Previous" @click.prevent="goPrevious">
          <span aria-hidden="true">&laquo;</span>
        </a>
        <a v-else class="page-link" href="#" aria-label="Previous" @click.prevent="goPrevious">
          <span aria-hidden="true">&laquo;</span>
        </a>
      </li>
      <li class="page-item" v-for="page in totalPages" :key="page">
          <a class="page-link" href="#" @click.prevent="goToPage(page)">{{ page }}</a>
      </li>
      <li class="page-item">
        <a v-if="currentPage == totalPages" class="page-link disabled" href="#" aria-label="Next" @click.prevent="goNext">
          <span aria-hidden="true">&raquo;</span>
        </a>
        <a v-else class="page-link" href="#" aria-label="Next" @click.prevent="goNext">
          <span aria-hidden="true">&raquo;</span>
        </a>
      </li>
    </ul>
  </nav>
  <!-- Modal -->
  <div class="modal fade" id="staticBackdrop" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h1 v-if="isAddUser" class="modal-title fs-5" id="staticBackdropLabel">新增</h1>
          <h1 v-else class="modal-title fs-5" id="staticBackdropLabel">编辑</h1>

          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close" @click="editUser"></button>
        </div>
        <div class="modal-body">
          <form>
            <div class="row mb-3" v-if="isAddUser">
              <div class="col-2">
                <label  class="form-label">用户名:</label>
              </div>
              <div class="col-10">
                <input  type="text" class="form-control" placeholder="请输入" v-model="userdata.username" >
              </div>
            </div>
            <div class="row mb-3" v-if="isAddUser">
              <div class="col-2">
                <label  class="form-label">密码:</label>
              </div>
              <div class="col-10">
                <input  type="password" class="form-control" placeholder="请输入" v-model="userdata.password" >
              </div>
            </div>
            <div class="row mb-3">
              <div class="col-2">
                <label class="form-label">姓名:</label>
              </div>
              <div class="col-10" >
                <input type="text" class="form-control" placeholder="请输入" v-model="userdata.nickname">
              </div>
            </div>
            <div class="row mb-3">
              <div class="col-2">
                <label class="form-label">性别:</label>
              </div>
              <div class="col-10" >
                <select class="form-select" placeholder="123" v-model="userdata.gender">
                  <option value="男">男</option>
                  <option value="女">女</option>
                </select>              
              </div>
            </div>
            <div class="row mb-3">
              <div class="col-2">
                <label class="form-label">年龄:</label>
              </div>
              <div class="col-10" >
                <input type="text" class="form-control" placeholder="请输入" v-model="userdata.age">
              </div>
            </div>
            <div class="row mb-3">
              <div class="col-2">
                <label class="form-label">学历:</label>
              </div>
              <div class="col-10">
                <select class="form-select" placeholder="123" v-model="userdata.eq">
                  <option value="高中">高中</option>
                  <option value="本科">本科</option>
                  <option value="研究生">研究生</option>
                </select>
              </div>
            </div>
            <div class="row mb-3">
              <div class="col-2">
                <label class="form-label">时薪:</label>
              </div>
              <div class="col-10" >
                <input type="text" class="form-control" placeholder="请输入" v-model="userdata.hprice">
              </div>
            </div>
            <div class="row mb-3">
              <div class="col-2">
                <label class="form-label">状态:</label>
              </div>
              <div class="col-10">
                <select class="form-select"  v-model="userdata.status">
                  <option value="上架">上架</option>
                  <option value="下架">下架</option>
                </select>
              </div>
            </div>
            <div class="row mb-3">
              <div class="col-2">
                <label class="form-label">邮箱:</label>
              </div>
              <div class="col-10">
                <input type="text" class="form-control" placeholder="请输入" v-model="userdata.email">
              </div>
            </div>
            <div class="row mb-3">
              <div class="col-2">
                <label class="form-label">手机号:</label>
              </div>
              <div class="col-10">
                <input type="text" class="form-control" placeholder="请输入" v-model="userdata.phonenumber">
              </div>
            </div>
            <div class="row mb-3">
              <div class="col-2">
                <label class="form-label">简介:</label>
              </div>
              <div class="col-10">
                <textarea class="form-control" id="remark" rows="3" v-model="userdata.remark"></textarea>
              </div>
            </div>
            </form>
        </div>
        <div v-if="isAddUser" class="error-message">{{ error_message }}</div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" @click="resetEditId">取消</button>
          <button v-if="isAddUser" type="button" class="btn btn-primary"  id="add_user_confirm_btn" @click="addUser">确定</button>
          <button v-else type="button" class="btn btn-primary"  data-bs-dismiss="modal" @click="updateUserInfo(editId)">确定</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from'jquery'
import {reactive ,ref,computed } from 'vue'
import { useStore } from 'vuex';
export default {
  name: 'UserView',
  setup() {
      let store = useStore()
      const responseDatas = ref([])
      const totalPages = ref(0) 
      const selectAll = ref(false); // 添加全选状态
      const currentPage = ref(1)  //页码
      const selectedIds = ref([]); // 添加已选行ID数组
      const userdata = reactive({
        username:'',
        password:'',
        nickname:'',
        province:'',
        status:'',
        email:'',
        phonenumber:'',
        eq:'',
        remark:'',
        hprice:'',
      })
      const isAddUser = ref(false); // 添加全选状态
      const editId = ref(0) //编辑的id
      const error_message = ref('')
      const searchData = ref('')  //搜索的值

      const headerMappingTableHeaders = {
        'id':'序号',
        'nickname':'昵称',
        'phonenumber':'手机号',
        'age':'年龄',
        'gender':'性别',
        'province':'所在地区',
        'remark':'简介',
        'status':'状态',
        'create_time':'创建时间',
        'operate':'操作',
      }

      const updateUserList = () => {
        $.ajax({
          url: "http://47.109.76.15:8000/teacher/teacherlist",
          type: "GET",
          data:{
            index:currentPage.value
          },
          success(resp){
            responseDatas.value = resp
            totalPages.value = Math.ceil(resp[0].count / 10)
          }      
      })
      }
      
      const filteredHeaders = computed(() => {
        return Object.keys(headerMappingTableHeaders)
          .filter(key => key !== 'id' && key !== 'operate');
      });
      

      const goPrevious = () => {
        if (currentPage.value > 1) {
          currentPage.value = currentPage.value - 1
          updateUserList();
        }
      }
      const goToPage = (i) =>{
        currentPage.value = i
        updateUserList()
      }
      const goNext = () => {
        if (currentPage.value < totalPages.value) {
          currentPage.value = currentPage.value + 1
          updateUserList();
        }
      }
      //模态框数据userdata
      const updateUserData = (data) => {
        userdata.username = data.username
        userdata.nickname = data.nickname
        userdata.role= data.role
        userdata.status= data.status
        userdata.email= data.email
        userdata.eq = data.eq
        userdata.phonenumber= data.phonenumber
        userdata.province= data.province
        userdata.remark= data.remark
        userdata.hprice= data.hprice
        userdata.age= data.age
        userdata.gender= data.gender
      }
      //更新模态框数据userdata
      const updateUser = (userId) => {
        editId.value = userId
        $.ajax({
            url:'http://47.109.76.15:8000/teacher/teacherinfo/',
            type:'get',
            data:{
              'user_id':userId
            },
            success(resp){
              updateUserData(resp)
              console.log(resp)
            },
          })
      }
      const updateUserInfo = (userId) =>{
        console.log('updateUserInfo------',userdata)
        $.ajax({
            url:'http://47.109.76.15:8000/teacher/teacherinfo/',
            type:'post',
            data:{
              'user_id':userId,
              'nickname':userdata.nickname,
              'status':userdata.status === '上线'?0:1,
              'email':userdata.email,
              'phonenumber':userdata.phonenumber,
              'hprice':userdata.hprice,
              'eq':userdata.eq === '高中' ? 2:userdata.eq === '本科'?3:4,
              'gender':userdata.userdata,
              'userdata':userdata.age,
              'remark':userdata.remark,
            },
            headers: {
              'Authorization': "Bearer " + store.state.user.access,
            },
            success(resp){
              updateUserList()
              console.log('updateUserInfo----',resp)
            },
          })
      }
      //新增用户
      const addUser = () =>{
        error_message.value = ''
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        if(userdata.username == '' && userdata.password == ''){
          error_message.value = "用户名或密码不能为空";
        }else if(userdata.email != '' && !emailRegex.test(userdata.email)){
          error_message.value = "邮箱格式不正确";
        }
        else{
          $.ajax({
          url:'http://127.0.0.1:8000/teacher/register/',
          type:'post',
          data:{
              'username':userdata.username,
              'password':userdata.password,
              'password_confirm':userdata.password,
              'nickname':userdata.nickname,
              'status':userdata.status === '上架'?0:1,
              'email':userdata.email,
              'phonenumber':userdata.phonenumber
          },
          headers: {
            'Authorization': "Bearer " + store.state.user.access,
          },
          success(resp){
              console.log(resp)
              $('#staticBackdrop').hide()
              updateUserList()
          }
        })
        }
      }
      //编辑用户
      const editUser = () =>{
        isAddUser.value = false
      }
      const toAddUser = () => {
        editId.value = 0
        userdata.username = ''
        userdata.nickname = ''
        userdata.password = ''
        userdata.gender= '男'
        userdata.status= '上架'
        userdata.hprice = ''
        userdata.remark = ''
        userdata.age = ''
        userdata.eq = '高中'
        userdata.email = ''
        userdata.phonenumber = ''
        isAddUser.value = true
      }
      const deleteUser = (index) =>{
        $.ajax({
          url: "http://47.109.76.15:8000/teacher/teacherinfo/",
          type: "delete",
          data:{
            'user_id': index
          },
          headers:{
            'Authorization': "Bearer " + store.state.user.access,
          },
          success(resp){
            console.log('delete',resp)
            updateUserList()
          }
        })
      }
      //搜索键按下的函数
      const search = () =>{
        console.log(searchData.value,'-------searchData.value')
        currentPage.value = 1
        if(searchData.value == ''){
          updateUserList()
        }else{
          $.ajax({
            url: "http://47.109.76.15:8000/teacher/search/",
            type:'GET',
            data:{
              data:searchData.value,
              index:currentPage.value
            },
            success(resp){
              console.log('search : ',searchData.value," success")
              responseDatas.value = resp
              totalPages.value = Math.ceil(resp[0].count / 10)
            }
          })
        }

      }
      updateUserList()
      return {
        totalPages,
        headerMappingTableHeaders,
        responseDatas,
        filteredHeaders,
        selectAll,
        // toggleSelectAll,
        selectedIds,
        goPrevious,
        goToPage,
        goNext,
        currentPage,
        userdata,
        updateUser,
        isAddUser,
        addUser,
        editUser,
        editId,
        updateUserInfo,
        error_message,
        toAddUser,
        deleteUser,
        search,
        searchData
      };
  },
};
</script>

<style scoped>

.table-container { /* 设置固定高度和滚动条 */
  width: 100%;
  height: 580px; 
  overflow-y: auto; 
}

.btn{
  margin-right: 20px;
}
.pagination{
  float: right;
  margin-right: 10px;
}
.error-message {
  margin: auto;
  color: red;
}
.disabled {
  pointer-events: none; /* 禁止点击 */
  opacity: 0.65;        /* 可选：降低透明度以表示禁用 */
  cursor: not-allowed;  /* 可选：改变鼠标悬停时的光标样式 */
}
th{
  font-size:16px;
  text-align: center; /* 使文字水平居中 */
}
td{
  font-size:14px;
  text-align: center; /* 使文字水平居中 */
}
</style>