<template>
    <div class="container table-container">
        <div class="row">
            <div class="col-12">
                <div ref="chartRef3" style="height: 400px;"></div>
            </div>
        </div>
        <div class="row">
            <div class="col-6">
                <div ref="chartRef1" style="height: 400px;"></div>
            </div>
            <div class="col-6">
                <div ref="chartRef2" style="height: 400px;"></div>
            </div>
        </div>
    </div>
</template>

<script>
import { onMounted, ref } from 'vue';
import * as echarts from 'echarts';
import $ from 'jquery'
export default {
  name: 'OverView',
  components: {
  },
  setup() {
    const chartRef1 = ref(null);
    const chartRef2 = ref(null); 
    const chartRef3 = ref(null); 
    onMounted(() => {
      // 初始化第一个图表（柱状图）
      const chartDom1 = chartRef1.value;
      const myChart1 = echarts.init(chartDom1);
      const option1 = {
        title: {
          text: '热门家教排名'
        },
        tooltip: {},
        xAxis: {
          data: ['刘亦菲', '赵丽颖', '刘诗诗', '李刚', '张雪莹', '张晓梅', '萧红', '刘老师', '张爱玲', '刘德华']
        },
        yAxis: {},
        series: [
          {
            name: '销量',
            type: 'bar',
            data: [2033, 1030, 506, 371, 253, 251, 179, 172, 126, 110]
          }
        ]
      };
      myChart1.setOption(option1);

      // 初始化第二个图表（饼状图）
      const chartDom2 = chartRef2.value;
      const myChart2 = echarts.init(chartDom2);
      const option2 = {
        title: {
          text: '热门科目比例',
          left: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        legend: {
          orient: 'vertical',
          left: 'left'
        },
        series: [
          {
            name: '销量',
            type: 'pie',
            radius: '50%',
            data: [
              { value: 5, name: '音乐' },
              { value: 6, name: '书法' },
              { value: 9, name: '艺术' },
              { value: 8, name: '小学' },
              { value: 10, name: '初中' },
              { value: 24, name: '高中' },

            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      };
      myChart2.setOption(option2);

      let dates = ref([])
      let ip_address = ref([])
      let visits = ref([])

      $.ajax({
        url:'http://127.0.0.1:8000/overview/count/',
        type:'get',
        success(resp){
          // 将对象的键值对转换为一个数组
          let entries = Object.entries(resp);
          // 反转数组
          entries.reverse();
          // 基于反转后的数组创建一个新的对象
          let reversedResp = Object.fromEntries(entries);
          console.log('resp.reverse()',reversedResp)
          dates.value = Object.keys(reversedResp)
          ip_address.value = Object.values(reversedResp).map(dateData => dateData.ip_address);
          visits.value = Object.values(reversedResp).map(dateData => dateData.total_requests);
          const chartDom3 = chartRef3.value;
          const myChart3 = echarts.init(chartDom3);
          const option3 = {
            title: {
              text: '最近一周访问量',
            },
            tooltip: {
                trigger: 'axis', // 可以是 'item' 或 'axis'，取决于你的需求
            },
            xAxis: {
                data: dates.value
            },
            yAxis: {},
            series: [
                {
                name: 'IP', // 系列名称
                data: ip_address.value,
                type: 'line',
                stack: 'x',
                itemStyle: {
                    color: 'red' 
                }
                },
                {
                name: 'Visit', // 系列名称
                data: visits.value,
                type: 'line',
                stack: 'x',
                itemStyle: {
                    color: 'black' 
                }
                }
            ]
            };
            myChart3.setOption(option3);
            },
      })

    });

    return {
      chartRef1,
      chartRef2, // 返回第二个图表的引用
      chartRef3
    };
  },
};
</script>

<style scoped>
  .table-container { /* 设置固定高度和滚动条 */
    width: 100%;
    height: 580px; /* 根据需要调整高度 */
    overflow-y: auto; /* 启用垂直滚动 */
    /* margin-bottom: 20px; 可选，为分页控件留出空间 */
  }
</style>